.modal{display:none}.modal.is-open{display:block}.modal-screen{position:fixed;background:rgba(0,0,0,0.7);top:0;bottom:0;left:0;right:0;z-index:2}.modal-container{position:absolute;top:100px;left:50%;margin-left:-220px;width:360px;padding:20px;background:#fff;z-index:3;border-radius:5px;border:1px solid #000}.modal-close{position:absolute;top:10px;right:20px;font-size:36px;line-height:1;cursor:pointer}.modal-close:hover{color:#526889}
